@font-face {
  font-family: 'icon';
  src: url('../font/icon.eot?94707974');
  src: url('../font/icon.eot?94707974#iefix') format('embedded-opentype'),
       url('../font/icon.woff2?94707974') format('woff2'),
       url('../font/icon.woff?94707974') format('woff'),
       url('../font/icon.ttf?94707974') format('truetype'),
       url('../font/icon.svg?94707974#icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
